@mixin transition-mixin {
  transition-duration: 0.3s;
}


@media (min-width: 150px) {
  .container {
    --column-count: 1;
  }
}

@media (min-width: 488px) {
  .container {
    --column-count: 2;
  }
}

@media (min-width: 768px) {
  .container {
    --column-count: 3;
  }
}

@media (min-width: 1024px) {
  .container {
    --column-count: 4;
  }
}
