.card {
  width: 200px;
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  padding: 32px 16px 16px;

  &:hover {
    transform: scale(1.2);
  }

  &__image {
    height: 134px;
    margin: 0 auto 40px;
    max-width: 160px;
    display: block;
  }

  &__title {
    margin-bottom: 4px;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: $text-color;

    @include transition-mixin;
  }

  &:hover &__title {
    color: #34568b;
  }

  &__code {
    margin: 0;
    padding: 0;
    margin-bottom: 16px;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: $color;
  }

  &__review-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
  }

  &__review {
    margin: 0;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: $text-color;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;

    &-label {
      margin: 0;
      font-weight: 400;
      font-size: 12px;
      line-height: 18px;
      color: $color;
    }

    &-amount {
      margin: 0;
      font-weight: 700;
      font-size: 16px;
      line-height: 18px;
      color: $text-color;
    }
  }

  &__button {
    display: block;
    box-sizing: border-box;
    width: 100%;
    height: 40px;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    color: $bg-color;
    background-color: $link-color;
    border-radius: 5px;
    border: 1px solid $link-color;
    outline: none;
    font-family: inherit;

    &:hover {
      background-color: $bg-color;
      color: $link-color;
      border: 1px solid $link-color;
      cursor: pointer;
    }
  }
}
